/*
COLORS

Primary: #eb2f64
Primary light:  #FF3366
Primary dark:   #BA265D

Grey light 1:   #faf9f9
Grey light 2:   #f4f2f2
Grey light 3:   #f0eeee
Grey light 4:   #ccc

Grey dark 1:    #333
Grey dark 2:    #777
Grey dark 3:    #999
*/

:root{
  --color-primary: #eb2f64;
  --color-primary-light:  #FF3366;
  --color-primary-dark:   #BA265D;

  --color-grey-light-1:   #faf9f9;
  --color-grey-light-2:   #f4f2f2;
  --color-grey-light-3:   #f0eeee;
  --color-grey-light-4:   #ccc;

  --color-grey-dark-1:    #333;
  --color-grey-dark-2:    #777;
  --color-grey-dark-3:    #999;

  --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, .3);

}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after{
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

html{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 62.5%; /* 1rem = 10px, 10px/16px = 62.5% (10px is what we want, 16px is browser default) */
}

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.6;
  color: var(--color-grey-dark-2);
  background-image: -webkit-gradient(linear, left top, right bottom, from(var(--color-primary-light)), to(var(--color-primary-dark)));
  background-image: -webkit-linear-gradient(left top, var(--color-primary-light), var(--color-primary-dark));
  background-image: -o-linear-gradient(left top, var(--color-primary-light), var(--color-primary-dark));
  background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
  background-size: cover;
  background-repeat: no-repeat;

  min-height: 100vh;
}

.App{
  max-width: 120rem;
  margin: 8rem auto;
  background-color: var(--color-grey-light-1);
  -webkit-box-shadow: var(--shadow-dark);
          box-shadow: var(--shadow-dark);

  min-height: 50rem;
}

@media only screen and (max-width: 1200px){
  .App{
    margin: 0;
    max-width: 100%;
  }
}

@media only screen and (max-width: 1100px){
  html{
    font-size: 50%;
  }
}


