.sidebar{
    background-color: var(--color-grey-dark-1);
    -ms-flex: 0 0 18%;
        flex: 0 0 18%;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.side-nav{
    font-size: 1.4rem;
    list-style: none;
    margin-top: 3.5rem;
}

.side-nav__item{
    position: relative;
}

.side-nav__item:not(:last-child){
    margin-bottom: .5rem;
}

.side-nav__item::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 3px;
    background-color: var(--color-primary);
    -webkit-transform: scaleY(0);
        -ms-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transition: width   .4s cubic-bezier(1,0,0,1) .2s,
                background-color .1s,
                -webkit-transform .2s;
    transition: width   .4s cubic-bezier(1,0,0,1) .2s,
                background-color .1s,
                -webkit-transform .2s;
    -o-transition: transform .2s,
                width   .4s cubic-bezier(1,0,0,1) .2s,
                background-color .1s;
    transition: transform .2s,
                width   .4s cubic-bezier(1,0,0,1) .2s,
                background-color .1s;
    transition: transform .2s,
                width   .4s cubic-bezier(1,0,0,1) .2s,
                background-color .1s,
                -webkit-transform .2s;
}

.side-nav__item:hover::before{
    -webkit-transform: scaleY(1);
        -ms-transform: scaleY(1);
            transform: scaleY(1);
    width: 100%;
}

.side-nav__item:active::before{
    background-color: var(--color-primary-dark);
}

.side-nav__link:link,
.side-nav__link:visited{
    color: var(--color-grey-light-1);
    text-decoration: none;
    text-transform: uppercase;
    display: block;
    padding: 1.5rem 3rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
    z-index: 10;
}

.side-nav__icon{
    margin-right: 2rem;
    fill: currentColor;
}

.legal{
    font-size: 1.2rem;
    color: var(--color-grey-light-4);
    text-align: center;
    padding: 2.5rem;
}

@media only screen and (max-width: 900px){
    .side-nav{
        display: -ms-flexbox;
        display: flex;
        margin: 0;
    }

    .legal{
        display: none;
    }

    .side-nav__item{
        -ms-flex: 1 1;
            flex: 1 1;
    }

    .side-nav__item:not(:last-child){
        margin-bottom: 0;
    }

    .side-nav__link:link,
    .side-nav__link:visited{
        -ms-flex-pack: center;
            justify-content: center;
        padding: 2rem;
    }
}

@media only screen and (max-width: 600px){
    .side-nav__link:link,
    .side-nav__link:visited{
        -ms-flex-direction: column;
            flex-direction: column;
        padding: 1.5rem .5rem;
    }

    .side-nav__icon{
        margin-right: 0;
        margin-bottom: .7rem;
        width: 1.5rem;
        height: 1.5rem;
    }
}


